import { RouterType } from '@/layouts/default';
import React, { useCallback } from 'react';
import { Link } from 'react-router-dom';
import Facebook from '../../assets/imgs/imgs/facebook_w.svg';
import Linkedin from '../../assets/imgs/imgs/linkedin_w.svg';
import Telegram from '../../assets/imgs/imgs/telegram.svg';
import Twitter from '../../assets/imgs/imgs/twitter_w.svg';
import './index.scss';

const Footer: React.FC<{ routers: RouterType; isMobile: boolean }> = ({
  routers,
  isMobile
}) => {
  const scrollTop = useCallback(() => {
    window.scroll(0, 0);
  }, []);

  return (
    <footer className="footer">
      {isMobile ? (
        <div>
          <Link
            style={{ marginBottom: '15px' }}
            className="bold-link"
            onClick={scrollTop}
            to={routers.home}
          >
            Home
          </Link>
          <Link className="bold-link" onClick={scrollTop} to={routers.solution}>
            Solutions
          </Link>
          <Link onClick={scrollTop} to={routers.dataCenterService}>
            Data Center Service
          </Link>
          <Link onClick={scrollTop} to={routers.cloudHosting}>
            Cloud Hosting
          </Link>
          <Link onClick={scrollTop} to={routers.softwareService}>
            Software Service
          </Link>
          <Link onClick={scrollTop} to={routers.webDevelopment}>
            Web Development
          </Link>
          <Link
            style={{ marginTop: '15px' }}
            className="bold-link"
            onClick={scrollTop}
            to={routers.aboutUs}
          >
            About Us
          </Link>
          <Link
            style={{ margin: '15px 0 45px' }}
            className="bold-link"
            onClick={scrollTop}
            to={routers.contactUs}
          >
            Contact Us
          </Link>
          <div className="copy">
            &copy; {new Date().getFullYear()} Coffee Cloud Company Limited
          </div>
        </div>
      ) : (
        <div className="container">
          <div className="block">
            <Link onClick={scrollTop} to={routers.home}>
              <div className="logo"></div>
            </Link>
            <div
              style={{
                display: 'block',
                width: '165px',
                height: '40px',
                lineHeight: '20px',
                fontSize: '12px',
                color: '#f0f0f0'
              }}
            >
              &copy; {new Date().getFullYear()} Coffee Cloud <br />
              Company Limited
            </div>
          </div>
          <div className="block">
            <div className="title">
              <Link onClick={scrollTop} to={routers.solution}>
                Solutions
              </Link>
            </div>
            <Link
              className="text"
              onClick={() => {
                window.scroll(0, 0);
              }}
              to={routers.dataCenterService}
            >
              Data Center Service
            </Link>
            <Link
              className="text"
              onClick={scrollTop}
              to={routers.cloudHosting}
            >
              Cloud Hosting
            </Link>
            <Link
              className="text"
              onClick={scrollTop}
              to={routers.softwareService}
            >
              Software Service
            </Link>
            <Link
              className="text"
              onClick={scrollTop}
              to={routers.webDevelopment}
            >
              Web Development
            </Link>
          </div>
          <div className="block" style={{ marginRight: 125 }}>
            <div className="title">
              <Link to={routers.aboutUs} onClick={scrollTop}>
                About us
              </Link>
            </div>
            <div className="title">
              <Link to={routers.contactUs} onClick={scrollTop}>
                Contact us
              </Link>
            </div>
            <div style={{ marginTop: 90 }}>
              <a
                className="icon"
                href="https://www.facebook.com/Coffee-Cloudio-100883849079709"
                target="_blank"
              >
                <img src={Facebook} alt="" />
              </a>
              <a
                className="icon"
                href="https://twitter.com/coffeecloud_io"
                target="_blank"
              >
                <img src={Twitter} alt="" />
              </a>
              <a
                className="icon"
                href="https://www.linkedin.com/company/coffee-cloud-hk/"
                target="_blank"
              >
                <img src={Linkedin} alt="" />
              </a>
            </div>
          </div>
          <div className="block" style={{ width: 360 }}>
            <div
              style={{
                width: 360,
                height: 25,
                fontStyle: 'normal',
                fontWeight: 700,
                fontSize: '15px',
                color: 'white',
                marginBottom: '20px'
              }}
            >
              Subscribe to Coffee Cloud{' '}
            </div>
            <div
              style={{
                width: 360,
                height: '40px',
                lineHeight: '20px',
                fontStyle: 'normal',
                fontWeight: 'normal',
                fontSize: '12px',
                color: '#f0f0f0'
              }}
            >
              Sign up to receive our newsletter to be kept
              <br /> up-to-date on our latest news and features.
            </div>
            <div style={{ marginTop: 60 }}>
              <input
                type="text"
                placeholder="Your email"
                style={{
                  backgroundColor: 'white',
                  padding: '5px 15px',
                  borderRadius: 5,
                  width: 360,
                  height: 45
                }}
              />
              <img
                className="icon"
                style={{
                  position: 'relative',
                  top: -33,
                  left: 315,
                  cursor: 'pointer'
                }}
                src={Telegram}
                alt=""
              />
            </div>
          </div>
        </div>
      )}
    </footer>
  );
};

export default React.memo(Footer);
