import { RouterType } from '@/layouts/default';
import { DownOutlined } from '@ant-design/icons';
import { Dropdown, Menu as MenuList } from 'antd';
import React, { useCallback, useState } from 'react';
import { Link, useHistory, useLocation } from 'react-router-dom';
import './index.scss';

const Menu: React.FC<{ routers: RouterType; isMobile: boolean }> = ({
  routers,
  isMobile
}) => {
  const { pathname } = useLocation();
  const history = useHistory();
  const [expanded, setExpanded] = useState(false);

  const toLink = useCallback(
    (link: string) => {
      history.push(link);
      window.scroll(0, 0);
      setExpanded(false);
    },
    [history]
  );
  const menu = (
    <MenuList>
      <MenuList.Item key="1">
        <Link
          className={[
            'link-element',
            pathname === routers.dataCenterService ? 'active' : ''
          ].join(' ')}
          to={routers.dataCenterService}
          onClick={() => {
            window.scroll(0, 0);
          }}
        >
          Data Center Service
        </Link>
      </MenuList.Item>
      <MenuList.Item key="2">
        <Link
          className={[
            'link-element',
            pathname === routers.cloudHosting ? 'active' : ''
          ].join(' ')}
          to={routers.cloudHosting}
          onClick={() => {
            window.scroll(0, 0);
          }}
        >
          Cloud Hosting
        </Link>
      </MenuList.Item>
      <MenuList.Item key="3">
        <Link
          className={[
            'link-element',
            pathname === routers.softwareService ? 'active' : ''
          ].join(' ')}
          to={routers.softwareService}
          onClick={() => {
            window.scroll(0, 0);
          }}
        >
          Software Service
        </Link>
      </MenuList.Item>
      <MenuList.Item key="4">
        <Link
          className={[
            'link-element',
            pathname === routers.webDevelopment ? 'active' : ''
          ].join(' ')}
          to={routers.webDevelopment}
          onClick={() => {
            window.scroll(0, 0);
          }}
        >
          Web Development
        </Link>
      </MenuList.Item>
    </MenuList>
  );
  return (
    <header className="menu">
      <div className="container">
        <Link
          className={[
            'link-element',
            pathname === routers.home ? 'active' : ''
          ].join(' ')}
          to={routers.home}
          onClick={() => {
            window.scroll(0, 0);
          }}
        >
          <div className="logo"></div>
        </Link>

        {isMobile ? (
          <div className="menu-trigger" onClick={() => setExpanded(true)}></div>
        ) : (
          <div className="menu-links">
            <ul className="links">
              <li className="link-item">
                <Link
                  className={[
                    'link-element',
                    pathname === routers.home ? 'active' : ''
                  ].join(' ')}
                  to={routers.home}
                  onClick={() => {
                    window.scroll(0, 0);
                  }}
                >
                  Home
                </Link>
              </li>
              <li className="link-item">
                <Link
                  className={[
                    'link-element',
                    pathname === routers.solution ? 'active' : ''
                  ].join(' ')}
                  to={routers.solution}
                  onClick={() => {
                    window.scroll(0, 0);
                  }}
                >
                  <Dropdown overlay={menu}>
                    <span className="ant-dropdown-link">
                      Solutions <DownOutlined />
                    </span>
                  </Dropdown>
                </Link>
              </li>
              <li className="link-item">
                <Link
                  className={[
                    'link-element',
                    pathname === routers.aboutUs ? 'active' : ''
                  ].join(' ')}
                  to={routers.aboutUs}
                  onClick={() => {
                    window.scroll(0, 0);
                  }}
                >
                  About Us
                </Link>
              </li>
            </ul>
            <Link
              className={[
                'link-element',
                pathname === routers.contactUs ? 'active' : ''
              ].join(' ')}
              to={routers.contactUs}
              onClick={() => {
                window.scroll(0, 0);
              }}
            >
              <button
                style={{ width: '110px', height: '32px', borderRadius: '50px' }}
              >
                Contact Us
              </button>
            </Link>
          </div>
        )}
        {isMobile && expanded && (
          <div className="menu-fillscreen">
            <div
              className="close-menu"
              onClick={() => setExpanded(false)}
            ></div>
            <ul className="links">
              <li className="link-item" onClick={() => toLink(routers.home)}>
                <a
                  className={[
                    'link-element',
                    pathname === routers.home ? 'active' : ''
                  ].join(' ')}
                >
                  Home
                </a>
              </li>
              <li
                className="link-item"
                onClick={() => toLink(routers.solution)}
              >
                <a
                  className={[
                    'link-element',
                    pathname === routers.solution ? 'active' : ''
                  ].join(' ')}
                >
                  Solution
                </a>
              </li>
              <li
                className="link-item"
                onClick={() => toLink(routers.dataCenterService)}
              >
                <a
                  className={[
                    'link-element',
                    pathname === routers.dataCenterService ? 'active' : ''
                  ].join(' ')}
                >
                  Data Center Service
                </a>
              </li>
              <li
                className="link-item"
                onClick={() => toLink(routers.cloudHosting)}
              >
                <a
                  className={[
                    'link-element',
                    pathname === routers.cloudHosting ? 'active' : ''
                  ].join(' ')}
                >
                  Cloud Hosting
                </a>
              </li>
              <li
                className="link-item"
                onClick={() => toLink(routers.softwareService)}
              >
                <a
                  className={[
                    'link-element',
                    pathname === routers.softwareService ? 'active' : ''
                  ].join(' ')}
                >
                  Software Service
                </a>
              </li>
              <li
                className="link-item"
                onClick={() => toLink(routers.webDevelopment)}
              >
                <a
                  className={[
                    'link-element',
                    pathname === routers.webDevelopment ? 'active' : ''
                  ].join(' ')}
                >
                  Web Development
                </a>
              </li>
              <li className="link-item" onClick={() => toLink(routers.aboutUs)}>
                <a
                  className={[
                    'link-element',
                    pathname === routers.aboutUs ? 'active' : ''
                  ].join(' ')}
                >
                  About Us
                </a>
              </li>
              <li
                className="link-item"
                onClick={() => toLink(routers.contactUs)}
              >
                <a
                  className={[
                    'link-element',
                    pathname === routers.contactUs ? 'active' : ''
                  ].join(' ')}
                >
                  Contact Us
                </a>
              </li>
            </ul>
          </div>
        )}
      </div>
    </header>
  );
};

export default React.memo(Menu);
