import { Link } from 'react-router-dom';
import WebUnion from '@/assets/imgs/icon/wunion.svg';
import Aboutbg1 from '@/assets/imgs/imgs/aboutbg2.png';
import AboutSlide from '@/assets/imgs/imgs/slide.png';
import About1 from '@/assets/imgs/mobile/about_1.png';
import About2 from '@/assets/imgs/mobile/about_2.png';
import About3 from '@/assets/imgs/mobile/about_3.png';
import Title from '@/components/title';
import './index.scss';

const About = ({ isMobile, goSubmit }) => {
  return (
    <>
      {isMobile && <img width="100%" src={About1} />}
      <Title
        title="Who We Are"
        contentWidth={isMobile ? '100%' : '860px'}
        marginTop="50px"
        contentTextAlign="center"
        content={[
          'Our Coffee Cloud team is made up of exceptional, diverse and enthusiastic team members. We focus on exceeding expectations, adding value, implementing new technology and having fun. These core concepts, when combined with our values of diversity, respect and fairness are key to our business excellence.'
        ]}
      />
      {isMobile ? (
        <>
          <img width="100%" style={{ marginBottom: 100 }} src={About2} />
          {/* <img width="100%" src={About3} /> */}
        </>
      ) : (
        <div
          style={{
            height: '800px',
            margin: '40px auto',
            textAlign: 'center',
            position: 'relative'
          }}
        >
          <img src={Aboutbg1} alt="" />
        </div>
      )}
      <div className="white-c aboutblackbg">
        <div
          style={{
            width: isMobile ? '100%' : '1140px',
            margin: '0 auto',
            display: isMobile ? 'block' : 'flex',
            justifyContent: 'space-between'
          }}
        >
          <div>
            <div
              style={{
                textAlign: isMobile ? 'center' : 'left'
              }}
              className={isMobile ? 'font-32' : 'font-42'}
            >
              Coffee Cloud Mission
            </div>
            <div
              className="font-24"
              style={{
                width: isMobile ? '100%' : 420,
                marginTop: 30,
                marginBottom: isMobile ? 50 : 0,
                textAlign: isMobile ? 'center' : 'left'
              }}
            >
              <span
                style={{
                  textAlign: isMobile ? 'center' : 'left'
                }}
              >
                Learn more about our International Team paving the way for Data,
                Engineer and Hardware solutions.
              </span>
            </div>
          </div>
          <div
            style={{ width: isMobile ? '100%' : '620px', lineHeight: '24px' }}
          >
            <div
              style={{
                textAlign: isMobile ? 'center' : 'left'
              }}
            >
              Coffee Cloud is one of the leading Cloud Services and Digital
              Solution providers, located in Hong Kong in the heart of Asia.
            </div>
            <div
              style={{ marginTop: 30, textAlign: isMobile ? 'center' : 'left' }}
            >
              We pride ourselves on the absolute best Digital Solutions for our
              clients, be they Decentralized Applications, or industry-leading
              Data Center Services. We embrace different viewpoints and
              perspectives, allowing our International Team to create diverse,
              “Out Of The Box” Solutions to business problems.
            </div>
            <Link
              to={'/solution'}
              onClick={() => {
                window.scroll(0, 0);
              }}
            >
              <div className="aboutbtn">Learn More About Our Solutions</div>
            </Link>
          </div>
        </div>
      </div>
      <div className="aboutwant">
        {!isMobile && <img src={AboutSlide} />}
        <div className="text">
          <div className={isMobile ? 'font-32 font-bold' : 'font-42 font-bold'}>
            Want To Join Us?
          </div>
          <div className="font-24">
            "We’re looking for people who know their worth to join our Coffee
            Cloud family. If you’re full of bright ideas, ready to pull together
            as a team, work on some exciting projects with industry-leading
            technology, we’d love to chat with you.
          </div>
        </div>
        {isMobile && (
          <img style={{ marginTop: '100px' }} width="100%" src={AboutSlide} />
        )}
      </div>
      <div className="aboutPinkbg">
        <div
          className={isMobile ? 'font-32 white-c' : 'font-42 white-c'}
          style={{ textAlign: 'center', paddingTop: '65px' }}
        >
          Got Questions, Feedback Or Requests?
        </div>
        <div
          className={isMobile ? 'font-32 white-c' : 'font-42 white-c'}
          style={{ textAlign: 'center', paddingTop: '5px' }}
        >
          We’d Love To Hear From You.
        </div>
        <div
          className="font-16 white-c"
          style={{
            width: 'fit-content',
            margin: 'auto',
            paddingTop: '36px',
            cursor: 'pointer'
          }}
        >
          <span style={{ borderBottom: '1px solid white' }}>
            <Link
              to={'/contact'}
              onClick={() => {
                window.scroll(0, 0);
              }}
            >
              Contact Us
            </Link>
          </span>
          <img style={{ paddingLeft: 5 }} src={WebUnion} alt="" />
        </div>
      </div>
    </>
  );
};

export default About;
