import { Button, Col, Form, Input, Row, Select, Spin } from 'antd';
import 'antd/dist/antd.css';
import { useHistory } from 'react-router-dom';
import { useState, useCallback } from 'react';
import useFetch from 'use-http';
import './index.scss';

const { Item } = Form;

const Enquiry = [
  { label: 'Sales / Business Enquiry', value: '1' },
  { label: 'Product / Solution Enquiry', value: '2' },
  { label: 'Customer Service / Support', value: '3' },
  { label: 'Other Enquiry', value: '4' }
];
const Solution = [
  { label: 'Data Center Service', value: '1' },
  { label: 'Cloud Hosting Service', value: '2' },
  { label: 'Software Service', value: '3' },
  { label: 'Web Development Service', value: '4' },
  { label: 'Other Service', value: '5' }
];
const Industry = [
  { label: 'Accounting', value: '1' },
  { label: 'Administration & Office Support', value: '2' },
  { label: 'Advertising, Arts & Media', value: '3' },
  { label: 'Banking & Financial Services', value: '4' },
  { label: 'Call Centre & Customer Service', value: '5' },
  { label: 'Community Services & Development', value: '6' },
  { label: 'Construction', value: '7' },
  { label: 'Consulting & Strategy', value: '8' },
  { label: 'Design & Architechture', value: '9' },
  { label: 'Education & Training', value: '10' },
  { label: 'Engineering', value: '11' },
  { label: 'Agriculture, Animals & Conservation', value: '12' },
  { label: 'Government & Defence', value: '13' },
  { label: 'Healthcare & Medical', value: '14' },
  { label: 'Hospitality & Tourism', value: '15' },
  { label: 'Human Resources & Recruitment', value: '16' },
  { label: 'Information & Communication Technology', value: '17' },
  { label: 'Insurance & Superannuation', value: '18' },
  { label: 'Legal', value: '19' },
  { label: 'Manufacturing, Transport & Logistics', value: '20' },
  { label: 'Marketing & Communications', value: '21' },
  { label: 'Mining, Resources & Energy', value: '22' },
  { label: 'Real Estate & Property', value: '23' },
  { label: 'Retail & Consumer Products', value: '24' },
  { label: 'Sales', value: '25' },
  { label: 'Science & Technology', value: '26' },
  { label: 'Sport & Recreation', value: '27' },
  { label: 'Trades & Services', value: '28' }
];

const EmailTable = ({ isMobile }) => {
  const gutterSpan = isMobile ? 24 : 12;
  const btnGutterSpan = isMobile ? 8 : 10;
  const [loading, setLoading] = useState(false);
  const history = useHistory();
  const scrollTop = useCallback(() => {
    window.scroll(0, 0);
  }, []);
  const { post } = useFetch(process.env.REACT_APP_HTTP_BASE_URL);
  const onFinish = (values: any) => {
    const {
      Enquiry,
      company,
      contactNum,
      email,
      first,
      industry,
      last,
      msg,
      solution
    } = values;
    updateTopId(
      Enquiry,
      company,
      contactNum,
      email,
      first,
      industry,
      last,
      msg,
      solution
    );
    async function updateTopId(
      Enquiry,
      company,
      contactNum,
      email,
      first,
      industry,
      last,
      msg,
      solution
    ) {
      const sendmassage = await post('/coffee/insertCloudIntention', {
        companyName: company,
        contactNumber: contactNum,
        email,
        enquiryType: Enquiry,
        firstName: first,
        industry,
        lastName: last,
        message: msg,
        serverId: 1,
        solutionOfInterest: solution
      });
      history.push('/thank');
      scrollTop();
      if (sendmassage.code == 200) {
        // setLoading(true);
      }
    }
  };

  return (
    <div className="emailBox">
      <div>
        {loading && (
          <div className="loading">
            <Spin size="large" />
          </div>
        )}
        <Form
          layout="vertical"
          onFinish={onFinish}
          autoComplete="off"
          size="large"
        >
          <Row gutter={24}>
            <Col span={24}>
              <Item label="Enquiry Type" name="Enquiry">
                <Select
                  placeholder="Please select enquiry type"
                  options={Enquiry}
                ></Select>
              </Item>
            </Col>
          </Row>
          <Row gutter={24}>
            <Col span={24}>
              <Item label="Solution of Interest" name="solution">
                <Select
                  placeholder="Please select solution of interest"
                  options={Solution}
                ></Select>
              </Item>
            </Col>
          </Row>
          <Row gutter={24}>
            <Col span={gutterSpan}>
              <Item
                label="First Name"
                name="first"
                rules={[{ required: true, message: 'Please enter First Name' }]}
              >
                <Input></Input>
              </Item>
            </Col>
            <Col span={gutterSpan}>
              <Item
                label="Last Name"
                name="last"
                rules={[{ required: true, message: 'Please enter Last Name' }]}
              >
                <Input></Input>
              </Item>
            </Col>
          </Row>
          <Row gutter={24}>
            <Col span={gutterSpan}>
              <Item
                label="Email"
                name="email"
                rules={[
                  {
                    type: 'email',
                    message: 'The input is not valid E-mail!'
                  },
                  {
                    required: true,
                    message: 'Please input your E-mail!'
                  }
                ]}
              >
                <Input></Input>
              </Item>
            </Col>
            <Col span={gutterSpan}>
              <Item label="Contact Number" name="contactNum">
                <Input></Input>
              </Item>
            </Col>
          </Row>
          <Row gutter={24}>
            <Col span={gutterSpan}>
              <Item label="Company Name" name="company">
                <Input></Input>
              </Item>
            </Col>
            <Col span={gutterSpan}>
              <Item label="Industry" name="industry">
                <Select
                  options={Industry}
                  placeholder="Please select your industry"
                ></Select>
              </Item>
            </Col>
          </Row>
          <Row gutter={24}>
            <Col span={24}>
              <Item label="Message" name="msg">
                <Input.TextArea></Input.TextArea>
              </Item>
            </Col>
          </Row>
          <Row gutter={24}>
            <Col span={4} offset={btnGutterSpan}>
              <Item>
                <Button className="pinkbtn" htmlType="submit">
                  Submit
                </Button>
              </Item>
            </Col>
          </Row>
        </Form>
      </div>
    </div>
  );
};

export default EmailTable;
