import HomeCloud from '@/assets/imgs/icon/cloud.png';
import HomeData from '@/assets/imgs/icon/data.svg';
import HomeDataCenter from '@/assets/imgs/icon/datacentre.png';
import HomeHigh from '@/assets/imgs/icon/high.svg';
import HomeMultiple from '@/assets/imgs/icon/multiple.svg';
import HomeQuality from '@/assets/imgs/icon/quality.svg';
import HomeScale from '@/assets/imgs/icon/scale.svg';
import HomeSoftware from '@/assets/imgs/icon/software.png';
import HomeSsd from '@/assets/imgs/icon/ssd.svg';
import HomeUnion from '@/assets/imgs/icon/union.svg';
import HomeWeb from '@/assets/imgs/icon/web.png';
import Face from '@/assets/imgs/imgs/facebook.svg';
import Linkedin from '@/assets/imgs/imgs/linkedin.svg';
import HomeRight from '@/assets/imgs/imgs/right.png';
import Twitter from '@/assets/imgs/imgs/twitter.svg';
import ContentWrap from '@/components/content-wrap';
import DescriptionGrid from '@/components/description-grid';
import EmailTable from '@/components/email-table';
import HostingPlans from '@/components/hosting-plans';
import SubmitRequest from '@/components/submit-request';
import Title from '@/components/title';
import { Link } from 'react-router-dom';
import './index.scss';

const HomeContent2Data = [
  {
    img: HomeDataCenter,
    imgAlt: '',
    title: 'Data Center Service',
    content:
      'Through us, you can enjoy world-class architecture and facility management, with quality hosting service at a competitive price.',
    link: '/data'
  },
  {
    img: HomeCloud,
    imgAlt: '',
    title: 'Cloud Hosting',
    content:
      'Our team’s vast knowledge and experience allows us to support your business goals, be it adopting Cloud Computing, CRM system or VoIP.',
    link: '/cloud'
  },
  {
    img: HomeSoftware,
    imgAlt: '',
    title: 'Software Service',
    content:
      'We have a great deal of experience in designing applications and software to improve your business process while meeting business goals and objects.',
    link: '/software'
  },
  {
    img: HomeWeb,
    imgAlt: '',
    title: 'Web Development',
    content:
      'We provide both client-side and server-side coding services. We also initiate the coding phase of development, making our developers & teams work to the highest qualified.',
    link: '/web'
  }
];

const gridData = [
  {
    title: 'Connect Multiple Servers',
    src: HomeMultiple,
    descriptions: [
      'Keeping your important information safe within an internal network. We configure multiple servers and connect them together through internal IP for maximum security.'
    ]
  },
  {
    title: 'Fully SSD Drives',
    src: HomeSsd,
    descriptions: [
      'Comes standard with all Cloud Servers. High performance and IOPS for any demanding workloads you have.'
    ]
  },
  {
    title: 'High Availability',
    src: HomeHigh,
    descriptions: [
      'Using Software Defined Storage technology - You no longer have to think of how to maintain high uptime. All Cloud Servers will failover automatically when there is a fault within the same region.'
    ]
  },
  {
    title: 'Scale on Demand',
    src: HomeScale,
    descriptions: [
      'With our on demand portals, you get a leading real-time and pay per use cloud connectivity service.'
    ]
  },
  {
    title: 'High Service Quality',
    src: HomeQuality,
    descriptions: [
      'Guaranteed service quality with industry-leading SLAs covering: delivery, fault handling, availability, frame loss, latency and jitter.'
    ]
  },
  {
    title: 'Extend your Data Centre',
    src: HomeData,
    descriptions: [
      'Migrate your virtual machines workloads from your on-premise data center into a fully managed data center. Or run hybrid cloud by connecting workloads using virtual private network.'
    ]
  }
];

const Home = ({ goSubmit, isMobile }) => {
  return (
    <>
      {/* {!isMobile && (
        <div className="home-content-1">
          <div>
            <div>
              <div style={{ marginBottom: '5px' }} className="font-32">
                Get the latest from Coffee Cloud
              </div>
              <div className="font-20 grey-color">
                See all the things Coffee Cloud can do
              </div>
            </div>
            <div className="btn-wrap">
              <input type="email" placeholder="Enter you email address" />
              <button>Submit</button>
            </div>
          </div>
        </div>
      )} */}

      <ContentWrap paddingTop={isMobile ? '0px' : '60px'}>
        <div style={{ marginTop: 100 }}></div>
        <Title title="What We Do" />
        <div className="home-content-2">
          {HomeContent2Data.map((content, index) => (
            <div key={index}>
              <div className="img-wrap">
                <img src={content.img} alt={content.imgAlt} />
              </div>
              <div className="font-20 font-bold">{content.title}</div>
              <div
                className="font-16 grey-color"
                style={{ height: `${isMobile ? 'fit-content' : '150px'}` }}
              >
                {content.content}
              </div>
              <Link
                className="font-16 font-bold"
                to={content.link}
                onClick={() => {
                  window.scroll(0, 0);
                }}
              >
                <span
                  style={{
                    color: '#ec407a',
                    marginRight: 8,
                    borderBottom: '2px solid #ec407a',
                    paddingBottom: 2
                  }}
                >
                  Learn More
                </span>
                <img src={HomeUnion} alt="" />
              </Link>
            </div>
          ))}
        </div>
      </ContentWrap>
      <HostingPlans goSubmit={goSubmit} isMobile={isMobile} />
      <SubmitRequest />
      <ContentWrap>
        <Title marginTop="80px" title="Features & Benefits" />
        <DescriptionGrid
          itemHeight={isMobile ? '250px' : '240px'}
          items={gridData}
          titleWidth="100%"
        />
      </ContentWrap>
      <ContentWrap
        paddingTop="0"
        paddingRight="0"
        paddingBottom="0"
        paddingLeft="0"
      >
        <div className="home-content-4">
          <div className="homeAboutbg">
            <div
              style={{
                width: isMobile ? '100%' : '1140px',
                color: 'white',
                fontSize: isMobile ? '32px' : '42px',
                fontWeight: 700,
                position: 'absolute',
                top: '50%',
                left: '50%',
                transform: 'translate(-50% ,-60%)'
              }}
            >
              <div style={{ marginBottom: isMobile ? '30px' : 0 }}>
                About Coffee Cloud
              </div>
              <div
                style={{
                  width: isMobile ? 'calc(100% - 40px)' : '560px',
                  fontSize: isMobile ? '20px' : '26px',
                  lineHeight: isMobile ? '28px' : '32px',
                  marginTop: isMobile ? 0 : '36px',
                  fontWeight: 'normal'
                }}
              >
                <span>
                  Coffee Cloud is one of the leading Cloud Service providers in
                  Hong Kong. We pride ourselves on our Professional and Diverse
                  team, with more than 8 years of Service and Solution
                  experience. We support a variety of Enterprise-level Cloud and
                  Digital Solutions, for numerous International Customers and
                  Clients.
                </span>
              </div>
              <img
                style={{ position: 'absolute', top: 10, right: 275 }}
                src={HomeRight}
                alt=""
              />
            </div>
          </div>
        </div>
      </ContentWrap>
      <div
        className="contact-wrap font-42 font-bold"
        style={{ margin: '100px auto 40px', width: 'fit-content' }}
      >
        Get In Touch With Us
      </div>
      <EmailTable isMobile={isMobile} />
      <div className="font-12 contactBottom">
        By clicking submit, you consent to allow Coffee Cloud to store and
        process the personal information submitted above to provide you the
        content requested.
        <div
          style={{
            width: '210px',
            margin: '26px auto 85px',
            display: 'flex',
            justifyContent: 'space-between'
          }}
        >
          <a
            target="_blank"
            href="https://www.facebook.com/Coffee-Cloudio-100883849079709"
          >
            <img src={Face} alt="" />
          </a>
          <a target="_blank" href="https://twitter.com/coffeecloud_io">
            <img src={Twitter} alt="" />
          </a>
          <a
            target="_blank"
            href="https://www.linkedin.com/company/coffee-cloud-hk/"
          >
            <img src={Linkedin} alt="" />
          </a>
        </div>
      </div>
    </>
  );
};

export default Home;
